import React, { Component } from 'react';

class BookListComponent extends Component {

    defaultBookImg(e){
        e.target.src = 'https://lh5.ggpht.com/RzF8JAZMFQKX0x-2vcUAO7LVZ_6ZUtObK7Uxn2rwquO2c7MRohfROcrZQwPJAXVHRhU';
    }

    render(){
        const componentData = this.props.parentdata;
        //console.log(componentData);
        return(
            <div>
                <div className="container">
                     <input className="form-control" placeholder="搜索课号/资料名称" type="text" />
                </div>
            <div style={{marginTop: '5%'}} >
                {
                    componentData.map((data,index) => {
                        return (
                            <div key={index} style={{marginTop: '1%'}}>
                                <a href={"book/bookdetail/" + data.id} className="list-group-item list-group-item-action">
                                    <div className="row">
                                        <div className="col-4 col-sm-4 col-md-2 col-lg-2">
                                            <img style={{width: 100, height: 100}} src={"http://www.atyorku.ca/" + data.thumbnail_url} onError={this.defaultBookImg} />
                                        </div>
                                        <div className="col-8 col-sm-8 col-md-10 col-lg-10">
                                                <div className="column">
                                                <h5>{data.course_code_parent_title} {data.course_code_child_title}</h5>
                                                    {
                                                        Object.keys(data).length > 0 ?
                                                            <p style={{marginBottom: "0"}}>
                                                                <span className="badge badge-warning">{data.book_category_name}</span>
                                                                {data.is_e_document === "1" ? <span className="badge badge-info" style={{marginLeft: "5px"}}>电子版</span> : <span className="badge badge-danger" style={{marginLeft: "2%"}}>实体版</span> }
                                                            </p> : false
                                                    }
                                                    {
                                                        Object.keys(data).length > 0 ?
                                                            <p style={{marginTop: "10px", marginBottom: "0"}}>
                                                                {/**学期icon**/
                                                                    data.term_year === "0" && data.term_semester === "" ? "" :
                                                                        <span>{data.term_year !== "0" ? `${data.term_year}  ` : ""}{data.term_semester}</span>
                                                                }
                                                            </p> : false
                                                    }
                                                    {/**教授icon**/
                                                        Object.keys(data).length > 0 ?
                                                            <p style={{marginBottom: "0"}}>
                                                                <span>{data.prof_name === "" ? "" : data.prof_name}</span>
                                                            </p> : false
                                                    }
                                                    <p style={{
                                                        backgroundColor: "#dedede",
                                                        color: "#717171",
                                                        borderBottomLeftRadius: 5,
                                                        borderTopLeftRadius: 5,
                                                        borderTopRightRadius: 5,
                                                        borderBottomRightRadius: 5,
                                                        padding: 8,
                                                        marginTop: 10
                                                    }}><font size="1">{data.name}</font></p>
                                                    <span><font size="4" color="#ff1722">$ {data.price}</font><br />卖家: {data.alias}</span>
                                            </div>
                                        </div>
                                    </div>
                                </a>
                            </div>
                        )
                    })
                }
            </div>
            </div>
        )
    }
}

export default class bookHomePage extends Component {

    constructor(){
        super();
        this.state = {
            bookData:[],
            categoryData:[],
            pageSize: 20
        }
    }

    componentDidMount(){
        this.getBookCategoryData();
        this.getBookData();
    }

    getBookCategoryData(){
        fetch('http://www.atyorku.ca/admin/bookCategory/bookCategoryController.php?action=getListOfBookCategoryWithJson')
            .then(response => response.json())
            .then(responseData => {
                this.setState({ categoryData : responseData.result });
                console.log(this.state.categoryData[0])
            })
    }

    getBookData(){
        fetch('http://www.atyorku.ca/admin/book/bookController.php?action=getListOfBooksWithJson&pageSize=' + this.state.pageSize)
            .then(response => response.json())
            .then(responseData => {
                this.setState({ bookData : responseData.result });
                //console.log(this.state.bookData);
            })
    }

    loadMore(){
        this.setState(preState => ({
            pageSize: preState.pageSize + 20,
        }), this.getBookData)
    }

    render(){
        return (
            <div>
                <div className="text-center" style={{marginTop: '5%', marginLeft: "1%", marginRight: "3%"}}>
                    <h2>书铺</h2>
                    <p>Find what you want here</p>
                    <hr/>
                </div>
                <BookListComponent parentdata={this.state.bookData} />
                <div className="col-xs-12 col-sm-12 col-md-6 offset-md-3 col-lg-6 offset-lg-3" style={{marginTop: '2%'}}>
                    <button className="btn btn-dark btn-block" onClick={this.loadMore.bind(this)} style={{marginTop: '10px', marginBottom: '10px'}}>加载更多</button>
                </div>
            </div>
        );
    }
}


