import React, { Component } from 'react';
import HeaderComponent from '../../components/headerComponent';
import FooterComponent from '../../components/footerComponent';

class CategoryComponent extends Component {

    render(){
        const componentData = this.props.parentdata;
        const parentTitle = this.props.parentTitle;
        //console.log(componentData);
        return(
            <div style={{marginTop: '5%', marginBottom: '5%'}} >
                {
                    componentData.map((data,index) => {
                        return (
                            <div key={index} style={{marginTop: '1%'}}>
                                <a href={"/courserating/detail/" + data.id} className="list-group-item list-group-item-action">
                                    <h5>{parentTitle} {data.title}</h5>
                                    <p>{data.full_title}</p>
                                </a>
                            </div>
                        )
                    })
                }
            </div>
        )
    }
}

export default class CourseChildPage extends Component {

    constructor(){
        super();
        this.state = {
            courseChildData: [],
            courseParentTitle: '',
            courseParentFullTitle: '',
            courseId: '',
        }
    }

    componentDidMount(){
        this.getChildCategory();
    }

    getChildCategory(){
        const parentId = this.props.params.courseParentId;
        //console.log(parentId);
        fetch("http://www.atyorku.ca/admin/courseCode/courseCodeController.php?action=getListOfChildCourseCodeByParentIdWithJson&course_code_parent_id=" + parentId)
            .then(response => response.json())
            .then(responseData => {
                if(responseData.code === 1){
                this.setState({
                    courseChildData: responseData.result,
                    //console.log(this.state.courseChildData);
                });
                this.getParentTitle();
            }else{
                    alert('访问列表出错!');
                }
            })
    }

    getParentTitle(){
        const parentId = this.props.params.courseParentId;
        fetch("http://www.atyorku.ca/admin/courseCode/courseCodeController.php?action=getCourseCodeByIdWithJson&course_code_id=" + parentId)
            .then(response => response.json())
            .then(responseData => {
                if(responseData.code === 1){
                    this.setState({
                        courseParentTitle: responseData.result.title,
                        courseParentFullTitle: responseData.result.full_title,
                        courseId: responseData.result.id
                    })
                }else{
                    alert('获取父目录信息出错!');
                }
            })
    }

    render(){
        return(
            <div>
                <HeaderComponent/>
                <div className="container" style={{marginTop: '5%'}}>
                    <h2>{this.state.courseParentTitle}</h2>
                    <p>{this.state.courseParentFullTitle}</p>
                    <hr/>
                    <CategoryComponent parentTitle={this.state.courseParentTitle} parentdata={this.state.courseChildData} />
                </div>
                <FooterComponent/>
            </div>
        );
    }
}


