import React, { Component } from 'react';
import HeaderComponent from '../../components/headerComponent';
import FooterComponent from "../../components/footerComponent";

export default class LoginPage extends Component {

    constructor(){
        super();
        this.state = {
            loginUsername: '',
            loginPassword: '',
            loginData: [],
            loginMessage: []
        }
    }

    loginAction(){
        //console.log('Username: ' + this.state.loginUsername, 'Password: ' + this.state.loginPassword)
        fetch('http://www.atyorku.ca/admin/login/loginController.php?action=userLogin', {
            method: 'post',
            headers: {
                'Content-type': 'application/x-www-form-urlencoded, text/plain'
            },
            body: JSON.stringify({
                username: this.state.loginUsername,
                password: this.state.loginPassword
            })
        }).then(response => response.json())
            .then(responseData => {
                if(responseData.code === 1){
                    this.setState({
                        loginData: responseData.result,
                    });
                }else{
                    this.setState({
                        loginMessage: responseData.message,
                    });
                    console.log('Login Error: ' + this.state.loginMessage);
                    return (
                        <div className="alert alert-danger" role="alert" style={{textAlign: 'center'}}>
                            {this.state.loginMessage}
                        </div>
                    )
                }

            });
    }

    onUsernameChanged(event){
        console.log('username: ' + event.target.value);
        this.setState({ loginUsername: event.target.value});
    }

    onPasswordChanged(event){
        console.log('password: ' + event.target.value);
        this.setState({ loginPassword: event.target.value});
    }

    render(){
        return (
            <div>
                <HeaderComponent/>
                <div className="container" style={{marginTop: '5%', marginBottom: '23%'}}>
                    <h2>Login Your Account</h2>
                    <form>
                        <div className="form-group" style={{marginTop: '5%'}}>
                            <label>Username:</label>
                            <input type="text" className="form-control" value={this.state.loginUsername} placeholder="Username" onChange={this.onUsernameChanged.bind(this)}/>
                                <small className="form-text text-muted">We'll never share your information with anyone else.</small>
                        </div>
                        <div className="form-group">
                            <label>Password:</label>
                            <input type="password" className="form-control" value={this.state.loginPassword} placeholder="Password" onChange={this.onPasswordChanged.bind(this)}/>
                        </div>
                    </form>
                    <button className="btn btn-success" onClick={this.loginAction.bind(this)} >Login</button>
                </div>
                <FooterComponent/>
            </div>
        );
    }
}


