import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
//import App from './App';
import registerServiceWorker from './registerServiceWorker';
import '../node_modules/bootstrap/dist/css/bootstrap.css';
import 'react-circular-progressbar/dist/styles.css';
import { Route, Router, IndexRoute, browserHistory } from 'react-router';

import MainPage from './pages/MainPage';
import HomePage from './pages/HomePage';
import LoginPage from './pages/user/loginPage';
import BookHomePage from './pages/book/bookHomePage';
import BookDetailPage from './pages/book/bookDetailPage';
import CourseCategoryPage from './pages/courseRating/courseCategoryPage';
import CourseChildPage from './pages/courseRating/courseChildPage';
import CourseDetailPage from './pages/courseRating/courseDetailPage';

class App extends Component {
    render(){
        return(
            <Router history={browserHistory}>
                <Route path="/" component={MainPage}>
                    <IndexRoute component={HomePage} />
                    <Route path="login" component={LoginPage} />
                    <Route path="courserating" component={CourseCategoryPage} />
                    <Route path="courserating/:courseParentId" component={CourseChildPage} />
                    <Route path="courserating/detail/:courseId" component={CourseDetailPage} />
                    <Route path="book" component={BookHomePage} />
                    <Route path="book/bookdetail/:bookId" component={BookDetailPage} />
                </Route>
            </Router>
        )
    }
}

ReactDOM.render(<App />, document.getElementById('root'));
registerServiceWorker();



