import React, { Component } from 'react';
import HeaderComponent from '../../components/headerComponent';
import FooterComponent from '../../components/footerComponent';

class CategoryComponent extends Component {

    render(){
        const componentData = this.props.parentdata;
        //console.log(componentData);
        return(
            <div style={{marginTop: '5%', marginBottom: '5%'}} >
                {
                    componentData.map((data,index) => {
                        return (
                            <div key={index} style={{marginTop: '1%'}}>
                                <a href={"/courserating/" + data.id} className="list-group-item list-group-item-action">
                                    <h4>{data.title}</h4>
                                    <p>{data.full_title}</p>
                                </a>
                            </div>
                        )
                    })
                }
            </div>
        )
    }
}

export default class CourseCategoryPage extends Component {

    constructor(){
        super();
        this.state = {
            courseCategoryData: [],
        }
    }

    componentDidMount(){
        this.getCourseCategory();
    }

    getCourseCategory(){
        fetch('http://www.atyorku.ca/admin/courseCode/courseCodeController.php?action=getListOfParentCourseCodeWithJson')
            .then(response => response.json())
            .then(responseData => {
                this.setState({ courseCategoryData : responseData.result });
                //console.log(this.state.courseCategoryData);
            })
    }

    render(){
        return (
            <div>
                <HeaderComponent/>
                <div className="container" style={{marginTop: '5%'}}>
                    <h2>课评目录</h2>
                    <p>CourseRating Page</p>
                    <hr/>
                    <CategoryComponent parentdata={this.state.courseCategoryData} />
                </div>
                <FooterComponent/>
            </div>
        );
    }
}


