import React, { Component } from 'react';
import HeaderComponent from '../../components/headerComponent';
import FooterComponent from '../../components/footerComponent';
import { Col } from 'react-bootstrap';
import CircularProgressbar from 'react-circular-progressbar';

class DetailComponent extends Component {

    render(){
        const homework_diff = this.props.homeworkDiff*10;
        const test_diff = this.props.testDiff*10;
        const content_diff = this.props.contentDiff*10;
        const overall_diff = this.props.overallDiff*10;
        const avg_grade = this.props.avg;
        console.log('课程: ' + content_diff);
        console.log('作业: ' + homework_diff);
        console.log('考试: ' + test_diff);
        console.log('平均成绩: ' + avg_grade);

        return(
            <div>
                <div style={{display: 'flex', flexDirection: 'row'}}>
                    <Col xs={3} sm={3} lg={7}>
                            <small>课程:</small>
                            <div className='progress'>
                                <div className='progress-bar bg-danger'
                                     role='progressbar'
                                     aria-valuenow={content_diff}
                                     aria-valuemin='0'
                                     aria-valuemax='100'
                                     style={{width: content_diff + '%'}}>
                                </div>
                            </div>
                            <small>作业:</small>
                            <div className='progress'>
                                <div className='progress-bar bg-warning'
                                     role='progressbar'
                                     aria-valuenow={homework_diff}
                                     aria-valuemin='0'
                                     aria-valuemax='100'
                                     style={{width: homework_diff + '%'}}>
                                </div>
                            </div>
                            <small>考试:</small>
                            <div className='progress'>
                                <div className='progress-bar bg-info'
                                     role='progressbar'
                                     aria-valuenow={test_diff}
                                     aria-valuemin='0'
                                     aria-valuemax='100'
                                     style={{width: test_diff + '%'}}>
                                </div>
                            </div>
                    </Col>

                    <Col xs={3} sm={3} md={3} lg={5} style={{textAlign: 'center'}}>
                        <div style={{display: 'flex', flexDirection: 'column'}}>
                            <Col>
                                <small>平均成绩: {avg_grade}</small>
                            </Col>
                            <Col xs={1} sm={1} md={12} lg={4}>
                                <CircularProgressbar percentage={overall_diff} className="progressbar-blue" textForPercentage={(pct)=> overall_diff/10} />
                            </Col>
                        </div>
                    </Col>

                </div>
            </div>
        )
    }
}

class CommentComponent extends Component {

    render(){
        const componentData = this.props.commentdata;
        //console.log(componentData);
        return(
            <div style={{marginTop: '5%', marginBottom: '5%'}} >
                {
                    componentData.map((data,index) => {
                        return (
                            <div key={index} style={{marginTop: '1%'}}>
                                <a href={"/courserating/detail/" + data.id} className="list-group-item list-group-item-action">
                                    <h5>{data.year} {data.term}</h5>
                                    <p>{data.comment}</p>
                                </a>
                            </div>
                        )
                    })
                }
            </div>
        )
    }
}

export default class CourseDetailPage extends Component {

    constructor(){
        super();
        this.state = {
            courseCommentData: [],
            courseProfessorData: [],
            courseDetailData: [],
            courseParentTitle: '',
            courseParentId: '',
            courseChildTitle: '',
            courseFullTitle: '',
            courseId: '',

            homework_diff: '',
            test_diff: '',
            content_diff: '',
            overall_diff: '',
            avg_grade: ''
        }
    }

    componentDidMount(){
        this.getCommentData();
    }

    getCommentData(){
        const id = this.props.params.courseId;
        //console.log(id);
        fetch("http://www.atyorku.ca/admin/courseRating/courseRatingController.php?action=getListOfCourseRatingByCourseIdWithJson&course_code_id=" + id + "&pageSize=20")
            .then(response => response.json())
            .then(responseData => {
                if(responseData.code === 1){
                    this.setState({
                        courseCommentData: responseData.result,
                        //console.log(this.state.courseDetailData);
                    });
                }else{
                    alert('没有找到相应课评');
                }
                this.getProfessorData();
                this.getDetailData();
            })
    }

    getProfessorData(){
        const id = this.props.params.courseId;
        fetch("http://www.atyorku.ca/admin/courseRating/courseRatingController.php?action=getListOfCourseProfReportWithJson&pageSize=20&prof_id=&course_code_id=" + id)
            .then(response => response.json())
            .then(responseData => {
                if(responseData.code === 1){
                    this.setState({
                        courseProfessorData: responseData.result
                    })
                }else{
                    alert('没有找到相应教授信息');
                }
            })
    }

    getDetailData(){
        const id = this.props.params.courseId;
        fetch("http://www.atyorku.ca/admin/courseRating/courseRatingController.php?action=getCourseReportByCourseIdWithJson&course_id=" + id)
            .then(response => response.json())
            .then(responseData => {
                if(responseData.code === 1){
                    this.setState({
                        courseDetailData: responseData.result,
                        courseParentTitle: responseData.result.course_code_parent_title,
                        courseParentId: responseData.result.course_code_parent_id,
                        courseChildTitle: responseData.result.course_code_child_title,
                        courseFullTitle: responseData.result.course_full_title,
                        courseId: responseData.result.course_code_id,
                        homework_diff: responseData.result.homework_diff,
                        test_diff: responseData.result.test_diff,
                        content_diff: responseData.result.content_diff,
                        overall_diff: responseData.result.overall_diff,
                        avg_grade: responseData.result.avg_grade
                    })
                }else{
                    alert('没有找到相应信息');
                }
            })
    }

    render(){
        return(
            <div>
                <HeaderComponent/>
                <div className="container" style={{marginTop: '5%'}}>
                    <h2>{this.state.courseParentTitle} <small>{this.state.courseChildTitle}</small></h2>
                    <p>{this.state.courseFullTitle}</p>
                    <hr/>
                    <DetailComponent homeworkDiff={this.state.homework_diff} testDiff={this.state.test_diff} contentDiff={this.state.content_diff} overallDiff={this.state.overall_diff} avg={this.state.avg_grade} />
                    <CommentComponent commentdata={this.state.courseCommentData} />
                </div>
                <FooterComponent/>
                </div>
        );
    }
}


